
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name:'addres',
components:{
Cell,
Group,
XInput,
XSwitch,
Selector,
XButton
},
data(){
return{
name:'',
phone:'',
addres:'',
partAddres:'',
btnMsg:'保存'
}
},
methods:{
save(){     //保存用户地址
if(this.name && this.phone && this.addres && this.partAddres){    //判断必填项是否填写
let changeAddres={};
changeAddres.namePhone = this.name+','+this.phone;     
changeAddres.addres = this.addres + this.partAddres;   
changeAddres.checked = false;      
changeAddres.name = this.name;
changeAddres.phone = this.phone;
changeAddres.addr = this.addres;
changeAddres.partAddres = this.partAddres;
this.$store.commit('saveAddres',changeAddres);
let _this = this  
_this.$vux.alert.show({
content: '保存成功',
onHide () {
_this.$router.push({name:'profile'});    
_this.$store.commit('isSaveAddres',true)  
_this.name = "";
_this.phone = "";
_this.addres = "";
_this.partAddres = "";
}
})
}else{
this.$vux.alert.show({
title: '注意',
content: '请将地址信息填写完整后再保存！',
})
}
}
},
created(){
this.$store.commit('keepAlive','addres')
},
beforeRouteLeave (to, from, next) {
if(to.path == '/profile'){
this.$store.commit('changekeepAlive','profile')
}
next()
}
}



